.header-component {
  position: fixed;
  z-index: 999;
  top: 0px;
  width: 100%;
  padding: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box; }
  .header-component .nav-icon {
    margin: 1em;
    width: 40px; }
  .header-component .nav-icon:after,
  .header-component .nav-icon:before,
  .header-component .nav-icon div {
    background-color: #fff;
    border-radius: 3px;
    content: '';
    display: block;
    height: 5px;
    margin: 7px 0;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out; }
  .header-component .nav-icon:hover:before {
    -webkit-transform: translateY(12px) rotate(135deg);
            transform: translateY(12px) rotate(135deg); }
  .header-component .nav-icon:hover:after {
    -webkit-transform: translateY(-12px) rotate(-135deg);
            transform: translateY(-12px) rotate(-135deg); }
  .header-component .nav-icon:hover div {
    -webkit-transform: scale(0);
            transform: scale(0); }

